<template>
<section>
  <div class="sign-box">
    <div class="sign-top-title">
    </div>
    <div class="sign-top-label">
        注 册
    </div>
    <el-row>
      <el-col :offset="5" :span="14">
        <el-form :rules="rules" ref="signForm" class="sign-form" :model="loginData">
          <el-form-item prop="name">
            <el-input class="myInput" v-model="loginData.name" placeholder="请输入医疗单位名称"></el-input>
          </el-form-item>
          <img class="hospital-icon" src="../../assets/imgs/hospital.png" alt="">
          <el-form-item prop="usercode">
            <el-input prefix-icon="el-icon-zhzy-user" @keydown.enter.native="handelLogin" auto-complete="off"
             v-model="loginData.usercode" placeholder="请输入账号" name="usercode"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input prefix-icon="el-icon-zhzy-lock" v-model="loginData.password"
             @keydown.enter.native="handelLogin"
            type="password" placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item prop="invitationCode">
            <el-input class="myInput" v-model="loginData.invitationCode" placeholder="请输入邀请码"></el-input>
          </el-form-item>
          <img class="invite-icon" src="../../assets/imgs/invite.png" alt="">
        </el-form>
        <el-checkbox v-model="checked"></el-checkbox>
        <span class="checked-info">同意 <i>注册协议</i></span>
      </el-col>
      <el-col align="center" :offset="5" :span="14"  style="margin-top: 26px">
        <el-button type="primary" @click="handelSign">注 册</el-button>
      </el-col>
    </el-row>
  </div>
</section>
</template>
<script>
import { 
  userRegister,
 } from "../../api";
export default {
  data(){
    return{
      loginData:{
        name: '',
        usercode: '',
        invitationCode: '',
        password: ''
      },
      options: [],
      checked: false,
      rules:{
        name:[{required: true, message: '医疗单位不能为空!', trigger: 'blur'}],
        usercode:[{required: true, message: '账号不能为空!', trigger: 'blur'}],
        invitationCode:[{required: true, message: '邀请码不能为空!', trigger: 'blur'}],
        password:[{required: true, message: '密码不能为空!', trigger: 'blur'}]
      },
    }
  },
  methods:{
    handelSign(){ // 提交按钮
      this.funCheckData()
    },
    funCheckData(){ // 表单验证
      this.$refs['signForm'].validate((valid) => {
        if(valid){
          this.funSign()
        }
      })
    },
    funSign(){ // 注册
      if(!this.checked){
        this.$message.error('请阅读并同意注册协议！')
        return
      }
      userRegister(this.loginData).then(res=>{
        if(res.data.code == '0'){
          this.$message.success('注册成功，请先登录!')
          this.$router.push('login')
        }else{
          this.$message.error(res.data.msg)
        }
      })
    },
  }
}
</script>
<style scoped>
.sign-box{
  margin:  0 auto;
  margin-top: 101px;
  width: 541px;
  height: 600px;
  border-radius: 6px;
  overflow: hidden;
  background-color: #fff;
}
.sign-top-title{
  height: 62px;
  background: url('../../assets/imgs/sign_title.png') no-repeat center;
  background-color: #BE8F61;
}
.sign-top-label{
  color: #BE8F61;
  padding: 22px;
  text-align: center;
  font-size: 18px;
}
.sign-form{
  position: relative;
}
.hospital-icon{
  position: absolute;
  width: 14px;
  top: 12px;
  left: 11px;
}
.invite-icon{
  position: absolute;
  width: 13px;
  top: 199px;
  left: 11px;
}
.checked-info{
  padding-left: 10px;
}
.checked-info i{
  color: #BE8F61;
}
</style>



